import React from 'react';
const Education = (props) => {
  const person = {
    education: [
        {degree: 'Bachelor of Engineering in Computer Engineering (International Program)', 
        institution: 'King Mongkut’s University of Technology Thonburi(KMUTT)', 
        startDate: '2010', endDate: '2014', description: 'GPAX 3.37'},
      ]};
  const myEducation = (
    <div>
      {person.education.map((edu) =>
        <div className='item' key={edu.degree}>
          <h3>{edu.degree} @ {edu.institution} <br/><span>{edu.startDate} - {edu.endDate}</span></h3>
          <p>{edu.description}</p>
        </div>
      )}
    </div>
  );
  return (
    <div className='title'>
      <i className='fa fa-graduation-cap'></i>
      <h2>EDUCATION</h2>
      {myEducation}
    </div>

  )
};

export default Education;



