import React from 'react';

const Skills = (props) => {
  //<div className='item w33' key={skll.name}>
  const person =  {skills: 
        { programming : [
            {name: 'HTML5', percentage: '85%'},
            {name: 'CSS', percentage: '90%'},
            {name: 'JavaScript', percentage: '85%'},
            {name: 'PHP', percentage: '80%'},
            {name: 'Unity3D', percentage: '95%'},
            {name: 'React', percentage: '70%'},
            {name: 'Python', percentage: '80%'},
            {name: 'C, C#', percentage: '95%'},
            {name: 'LUA, Basic', percentage: '90%'}
          ],
          language : [
            {name: 'Thai'},
            {name: 'English(TOEIC 625)'}
          ],
          other : [
            {name: 'Acting as a team player'},
            {name: 'Agile Management'},
            {name: 'Analytical and Research'},
            {name: 'Time and Priority management'},
            {name: 'Marketing'},
            {name: 'Good Listener'},
            {name: 'Problem-solving'},
            {name: 'Live sound engineering'}
          ]

        }
      };
  const mySkills = (
    <div>
      {person.skills.programming.map((skll) =>
        <div className='w33' key={skll.name}>
          <h3>{skll.name}</h3>
          <div className='skills'>
            <span style={{width: skll.percentage}}></span>
          </div>
        </div>
      )}
    </div>
  );
  const myLangs = (
    <div>
      {person.skills.language.map((skll) =>
        <div className='w33' key={skll.name}>
          <p>-{skll.name}</p>
        </div>
      )}
    </div>
  );
  const myOthers = (
    <div>
      {person.skills.other.map((skll) =>
        <div className='w33' key={skll.name}>
          <p>-{skll.name}</p>
        </div>
      )}
    </div>
  );


  return (
    <div className='title'>
      <i className='fa fa-code'></i>
      <h2>SKILLS</h2>
      <h3>Programing</h3>
      {mySkills}
      <h3>Language</h3>
      {myLangs}
      <h3>Other</h3>
      {myOthers}
    </div>
  )
};

export default Skills;



